(function (wpm, $, undefined) {

	wpm.saveSettingsToDisk = (version) => {
		let text             = document.getElementById("export-settings-json").value
		text                 = text.replace(/\n/g, "\r\n") // To retain the Line breaks.
		let blob             = new Blob([text], {type: "text/plain"})
		let anchor           = document.createElement("a")
		anchor.download      = "pixel-manager-settings_" + version + "_" + wpm.getCurrentDateForFileName() + ".json"
		anchor.href          = window.URL.createObjectURL(blob)
		anchor.target        = "_blank"
		anchor.style.display = "none" // just to be safe!
		document.body.appendChild(anchor)
		anchor.click()
		document.body.removeChild(anchor)
	}

	// Get date in year month day divided by dots. Month and day have to be zero padded.
	wpm.getCurrentDateForFileName = () => {
		let date  = new Date()
		let year  = date.getFullYear()
		let month = ("0" + (date.getMonth() + 1)).slice(-2)
		let day   = ("0" + date.getDate()).slice(-2)
		return year + "." + month + "." + day

		// return date.toLocaleDateString(
		// 	"en-US", {
		// 		year : "numeric",
		// 		month: "2-digit",
		// 		day  : "2-digit",
		// 	},
		// )
	}

	wpm.readSettingsFile = e => {

		let file = e.target.files[0]
		if (!file) return
		let reader    = new FileReader()
		reader.onload = function (e) {
			let contents = JSON.parse(e.target.result)

			// document.getElementById("import-settings-json").textContent = JSON.stringify(contents)

			wpm.saveImportedSettingsToDb(contents)
		}
		reader.readAsText(file)
	}

	wpm.saveImportedSettingsToDb = settings => {

		fetch(pmwAdminApi.root + "pmw/v1/settings/", {
			method     : "POST",
			credentials: "same-origin",
			headers    : {
				"Content-Type": "application/json",
				"X-WP-Nonce"  : pmwAdminApi.nonce,
			},
			body       : JSON.stringify(settings),
		})
			.then(response => response.json())
			.then(async message => {
				if (message.success) {
					console.log(message)
					// reload window
					document.getElementById("upload-status-success").style.display = "block"
					// wait 5 seconds
					await new Promise(resolve => setTimeout(resolve, 1000))
					window.location.reload()
				} else {
					console.log(message)
					document.getElementById("upload-status-error").style.display = "block"
				}
			})
			.catch(error => {
				console.error(error)
				document.getElementById("upload-status-error").style.display = "block"
			})
	}

	wpm.readGa4DataApiCredentials = e => {

		let file = e.target.files[0]
		if (!file) return
		let reader    = new FileReader()
		reader.onload = function (e) {
			let contents = JSON.parse(e.target.result)

			// document.getElementById("import-settings-json").textContent = JSON.stringify(contents)

			wpm.saveGa4DataApiCredentialsToDb(contents)
		}
		reader.readAsText(file)
	}

	wpm.deleteGa4DataApiCredentials = e => {
		wpm.saveGa4DataApiCredentialsToDb({})
	}

	wpm.saveGa4DataApiCredentialsToDb = credentials => {

		fetch(pmwAdminApi.root + "pmw/v1/ga4/data-api/credentials", {
			method     : "POST",
			credentials: "same-origin",
			headers    : {
				"Content-Type": "application/json",
				"X-WP-Nonce"  : pmwAdminApi.nonce,
			},
			body       : JSON.stringify(credentials),
		})
			.then(response => response.json())
			.then(async responseJson => {
				if (responseJson.success) {
					console.log(responseJson)
					// reload window
					document.getElementById("ga4-api-credentials-upload-status-success").style.display = "block"
					// wait 5 seconds
					// await new Promise(resolve => setTimeout(resolve, 5000))
					window.location.reload()
				} else {
					console.log(responseJson)
					document.getElementById("ga4-api-credentials-upload-status-error").style.display     = "block"
					// and add the error message
					document.getElementById("ga4-api-credentials-upload-status-error-message").innerHTML = "Error message: " + responseJson.data.message
				}
			})
			.catch(error => {
				console.error(error)
				document.getElementById("ga4-api-credentials-upload-status-error").style.display = "block"
			})
	}

}(window.wpm = window.wpm || {}, jQuery))

/**
 * This script is used to track the mouse position and set the transparency
 * of the button with which the pro-demo can be enabled.
 */
document.addEventListener("DOMContentLoaded", () => {

	// Get the pmw_pro_version_demo element and save it in a variable
	const elementToTrack = document.getElementById("pmw-pro-version-demo")

	// Only continue if the element exists
	if (!elementToTrack) {
		return
	}

	// This is a span element that contains two input elements.
	// Only continue if both are not checked.
	if (elementToTrack.querySelector("input:checked")) {
		return
	}

	const maxWidth = window.innerWidth / 2

	const calculateTheDistanceInPercent = (mousePosition, maxWidth) => {

		// Get the element position and save it in a variable
		const elementPosition = elementToTrack.getBoundingClientRect()
		const distance        = Math.sqrt(Math.pow(mousePosition.clientX - elementPosition.left, 2) + Math.pow(mousePosition.clientY - elementPosition.top, 2))

		return distance / maxWidth  // returns the percentage
	}

	const setTransparency = (percentage) => {

		if (percentage > 1) {
			percentage = 1
		}

		const maximumReduction = 80
		const reduction        = maximumReduction * percentage
		const transparency     = 100 - reduction

		elementToTrack.classList.add("pmw-opacity-transition")
		elementToTrack.style.opacity = transparency / 100
	}

	document.addEventListener("mousemove", event => {
		const distanceInPercent = calculateTheDistanceInPercent(event, maxWidth)
		setTransparency(distanceInPercent)
	})
})
